.gallery{
    background-color: black;
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-width: 30%;
    -moz-column-width: 30%;
    column-width: 30%;
    padding: 0 12px;
    width: 90%;
    margin: auto;
    padding-bottom: 20px;
}

.gallery .pics{
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
}

.gallery .pics:hover{
    filter: opacity(0.8);
}

@media (max-width: 991px){
    .gallery{
        background-color: black;
        -webkit-column-count: 2;
        -moz-column-count: 2;
        column-count: 2;
        padding: 0 12px;
    }
}

@media (max-width: 480px){
    .gallery{
        background-color: black;
        -webkit-column-count: 1;
        -moz-column-count: 1;
        column-count: 1;
        padding: 0 12px;
    }
}

.model{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items:center ;
    background-color: black;
    transition: opacity 0.4s ease, visibility 0.4s ease, transform 0.5s ease-in-out ;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}

.model.open{
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.model img{
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;

}

.model.open svg{
    position: fixed;
    width: 3rem;
    height: 3rem;
    padding: 5px;
    background-color: black;
    color: white;
    cursor: pointer;
}
.close{
top: 10px;
right: 10px;
}

.before{
    left: 10px;
}

.next{
    right: 10px;
}